@font-face {
  font-family: AvenirBook;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
  font-style: normal;
}

@font-face {
  font-family: AvenirHeavy;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 900;
  font-style: normal;
}

* {
  box-sizing: border-box;
  color: #3d4e61;
  font-family: AvenirBook, sans-serif;
}

.form {
  margin-top: 20px;
}

.input {
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  outline: none;
  width: 100%;
  padding: 23px 23px 23px 62px;
  font-size: 16px;
  box-shadow: 0 1px 8px #3d4e611a;
}

.input:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.input:focus {
  text-shadow: 1px 4px 4px #00000040;
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  font-family: AvenirHeavy, sans-serif;
}

.input__wrapper {
  position: relative;
}

.input__wrapper:after {
  content: "";
  pointer-events: none;
  background-image: url("Search.ccd3c07b.svg");
  background-size: cover;
  width: 19px;
  height: 19px;
  position: absolute;
  top: 25px;
  left: 26px;
}

[data-qa="small"] .input__wrapper:after {
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

[data-qa="small"] .input {
  padding: 10px 23px 10px 33px;
  font-size: 14px;
}

[data-qa="small"] .input:focus {
